import { StatusBar } from 'expo-status-bar';
import React, { useEffect, useState } from 'react';
import { StyleSheet, TouchableOpacity, View, ScrollView,TextInput,Switch,FlatList,Alert,
	Image } from 'react-native';
import { WebView } from 'react-native-webview';
import Layout from '../components/global/Layout';
import Text from '../components/utils/UbuntuFont';
import Colors from '../constants/colors';
import * as firebase from 'firebase';
import Slider from '@react-native-community/slider';

export default function ({ route, navigation }) {
	const dbRef = firebase.firestore().collection('devices');
	let [loading, setLoading] = useState(false);
	let [isEnabled, setIsEnabled] = useState(false);
	let [isAuto, setIsAuto] = useState(true);
	let [isLightSensor,setIsLightSensor] = useState(true);
	let [lightValue, setLightValue] = useState('');
	let [lightSensorValue, setLightSensorValue] = useState('');

	let [cameraState, setCameraState] = useState(true);
	let [lightSensorState, setLightSensorState] = useState(true);
	let [ledState, setLedState] = useState(true);
	let [networkState, setNetworkState] = useState(true);


	const deleteItem = async (id) => {
		await dbRef.doc(id).delete();
		console.log("Deleted ", id);
		navigation.navigate('Home');
	};

	const updateItem = async (id) => {
		await dbRef.doc(id).update();
		console.log("update ", id);
	  };

  
	const toggleAutoSwitch = () => {
		setIsAuto(previousState => !previousState);
		
		isAuto ? setMode('manual') : setMode('auto') 
	};
	const toggleLightSensorSwitch = () => {
		setIsLightSensor(previousState => !previousState);
		isLightSensor ?  setMode('no_light_sensor') : setMode('light_sensor')
	};

	const ip = route.params.serverIP;
	const name = route.params.name;
	const id = route.params.id;
	const key = route.params.key;

	useEffect(() => {
	  	fetch(ip+'/data')
		.then((response) => response.json())
		.then((data) => {
			setIsAuto(data.isAuto)
			setIsLightSensor(data.isLightSensor)
			setLightValue(data.lightValue)
			setLightSensorValue(data.lightSensorValue)
		})
		.catch((error) => console.error(error))
	}, []);

	function setMode(mode){
	    var data = new FormData();

		data.append('type',mode);
	
		fetch(ip+'/mode', {
			method: 'POST',
			headers: {
			  Accept: 'application/json',
			  'Content-Type': 'multipart/form-data'
			},
			body: data
		  })
		  .then((response) => {
			Alert.alert(mode+' 모드가 적용되었습니다.');
		  })
		  .catch((error) => {
			Alert.alert(mode+' 모드 적용에 실패 하였습니다.');
			console.error(error);
		  });
	  
	}


	function setLight(value){
		setLightValue(lightValue);
		
	    var data = new FormData();

		data.append('value',value);
	
		fetch(ip+'/light', {
			method: 'POST',
			headers: {
			  Accept: 'application/json',
			  'Content-Type': 'multipart/form-data'
			},
			body: data
		  })
		  .then((response) => {
			Alert.alert(value+' 밝기가 적용되었습니다.');
			setLightValue(lightValue);
		  })
		  .catch((error) => {
			Alert.alert(value+' 밝기 적용에 실패 하였습니다.');
			console.error(error);
		  });
	  
	}


	const mjpgurl = ip+"/frame.mjpg";

	function formatHtml () {
    return ('<html><body><img src="' + mjpgurl + '" width="100%" style="background-color: gray; min-height: 100%; min-width: 100%; position: fixed; top: 0; left: 0;"></body></html>');
	}

	return (
		<Layout navigation={navigation} title="상세보기" withBack>
				<ScrollView
					contentContainerStyle={{
						flexGrow: 1,
					}}
				>
					<View
						style={{
							flex: 1,
							justifyContent: 'center',
							alignItems: 'center',
							backgroundColor: '#f7f7f7',
						}}
					>
						<Text style={{fontSize:20}}>이름: {name}</Text>
						<Text style={{fontSize:20}}>IP: {ip}</Text>
						<WebView
							style={styles.backgroundVideo}
							automaticallyAdjustContentInsets={true}
							scalesPageToFit={true}
							startInLoadingState={false}
							contentInset={{top: 0, right: 0, left: 0, bottom: 0}}
							scrollEnabled={false}
							source={{html: formatHtml(), baseUrl: '/'}} />
					</View>
						<View
							style={{
								flex: 3,
								alignItems: 'center',
								paddingHorizontal: 20,
								paddingBottom: 20,
								backgroundColor: '#fff',
							}}
						>
							<Text style={{fontSize:20}}>LED 밝기:{Number(lightValue)} %</Text>
							<Text style={{fontSize:20}}>조 도:{Number(lightSensorValue)} LUX</Text>

						<View
							style={{
								flexDirection: 'row',
								alignItems: 'center',
								marginTop: 15,
								justifyContent: 'center',
							}}
						>

						<Text
							style={{
								fontSize:20,
								color: Colors.black,
							}}
						>
							조도센서 On/Off
						</Text>

						<Switch
							trackColor={{ false: "#767577", true: "#81b0ff" }}
							thumbColor={isLightSensor ? "#f5dd4b" : "#f4f3f4"}
							ios_backgroundColor="#3e3e3e"
							onValueChange={toggleLightSensorSwitch}
							value={isLightSensor}
						/>
						</View>
						<View
							style={{
								flexDirection: 'row',
								alignItems: 'center',
								marginTop: 15,
								justifyContent: 'center',
							}}
						>
							<Text
								style={{
									fontSize:20,
									color: Colors.black,
								}}
							>
								자동모드 On/Off
							</Text>

						<Switch
							trackColor={{ false: "#767577", true: "#81b0ff" }}
							thumbColor={isAuto ? "#f5dd4b" : "#f4f3f4"}
							ios_backgroundColor="#3e3e3e"
							onValueChange={toggleAutoSwitch}
							value={isAuto}
						/>


						</View>
						<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									marginTop: 15,
									justifyContent: 'center',
								}}
							>						
							<Text
								style={{
									fontSize:20,
									color: Colors.black,
								}}
							>
								밝기 값 조절
							</Text>

								<Slider
									disabled={isAuto}
									style={{width: 200, height: 40}}
									minimumValue={0}
									maximumValue={100}
									step={10}
									minimumTrackTintColor="#EE0032"
									maximumTrackTintColor="#333333"
									onSlidingComplete={setLight}
									onValueChange={setLightValue}
									value={Number(lightValue)}
								/>
							</View>						
						<TouchableOpacity
							onPress={() => {
								deleteItem(key);
							}}
							style={{
								backgroundColor: Colors.danger,
								padding: 20,
								paddingHorizontal: 120,
								marginTop: 10,
								borderRadius: 10,
							}}
						>
							<Text style={{ color: 'white', fontSize: 20}} bold>
								장비삭제
							</Text>
						</TouchableOpacity>

					</View>
				</ScrollView>		
		</Layout>
	);
}
const styles = StyleSheet.create({
	container: {
		flex: 1,
		flexDirection: 'column',
	},
	textInputContainer: {
		marginTop: 15,
		backgroundColor: '#FFF',
		borderColor: '#d8d8d8',
		borderWidth: 1,
		borderRadius: 8,
		flexDirection: 'row',
	},
	textInput: {
		padding: 10,
		paddingHorizontal: 20,
		textAlign: 'left',
		color: 'black',
		flex: 1,
		fontFamily: 'Ubuntu_400Regular',
	},
	button: {
		flex: 1,
		alignItems: 'center',
		justifyContent: 'center',
		padding: 15,
		backgroundColor: Colors.primary,
		borderRadius: 8,
	},
	backgroundVideo: {
		width:400,
		height:322
	  }	
});
