import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect } from 'react';
import { StyleSheet, TouchableOpacity, View, ScrollView,TextInput,  Alert,ActivityIndicator,
	Modal, Image } from 'react-native';
import { ListItem, Card } from 'react-native-elements'
import Layout from '../components/global/Layout';
import Text from '../components/utils/UbuntuFont';
import Colors from '../constants/colors';
import * as firebase from 'firebase';

  
export default function ({ navigation }) {

	const dbRef = firebase.firestore().collection('devices');
	const [name, setName] = useState('');
	const [ip, setIP] = useState('');
	const [loading, setLoading] = useState(false);
	const [modalVisible, setModalVisible] = useState(false);
	const [deviceList, setDeviceList] = useState([])

	useEffect(() => {
		listDevices();
	})
	function getDeviceList(querySnapshot){
		const deviceList = [];
		querySnapshot.forEach((res) => {
		const { name, ip } = res.data();
		deviceList.push({
			key: res.id,
			name,
			ip
		});
		});
		setLoading(false)
		setDeviceList(deviceList)
	}

	async function listDevices() {
		await dbRef.onSnapshot(getDeviceList);
	}

	async function storeDevice() {
		if(name === ''){
			Alert.alert('디바이스 이름이 없습니다!');
		} 
		else if (ip === ''){
			Alert.alert('IP주소가 없습니다!');
		}
		else {
			await dbRef.add({
				name: name,
				ip: ip
			}).then((res) => {
				setName('')
				setIP('')
				// setLoading(false);
				setModalVisible(!modalVisible);				
				Alert.alert("등록되었습니다.")
			})
			.catch((err) => {
				// setLoading(false);
				console.error("Error found: ", err);
				Alert.alert("에러발생.",err)
			});
		}
	};
	
	return (
		<Layout navigation={navigation} title="LED 리스트">
				{/* This text using ubuntu font */}
				<ScrollView
					contentContainerStyle={{
						flexGrow: 1,
					}}
				>
					<View
						style={{
							flex: 1,
							justifyContent: 'center',
							alignItems: 'center',
							backgroundColor: '#f7f7f7'
						}}
					>	
						{
						deviceList.map((item, i) => {
							return (
								<TouchableOpacity onPress={() =>
									navigation.navigate('SecondScreen',{key:item.key,name:item.name,id:item.id,serverIP:item.ip})
								}>
								<Card containerStyle={{}} wrapperStyle={{}} key={item.key}>
								<Card.Title>{i+1+'.'+item.name}</Card.Title>
								<Card.Divider />
								<View
								  style={{
									position: "relative",
									alignItems: "center",
									width:300
								  }}
								>
								  <Image
									style={{ width: "100%", height: 200 }}
									resizeMode="stretch"
									source={{uri:item.ip+"/frame.jpg"}}
								  />
								<Text style={{ color: Colors.warning, fontSize: 15 }}>
									{"IP: "+item.ip}
								</Text>

								</View>
							  </Card>
							</TouchableOpacity>
							);
						})
						}
						<Modal
							animationType="slide"
							transparent={true}
							visible={modalVisible}
							onRequestClose={() => {
							Alert.alert("Modal has been closed.");
							}}
						>
						<View style={{backgroundColor:"#000000aa"}}>
							<View style={styles.modalView}>
							<Text style={{ color: Colors.black, fontSize: 16 }}>이름</Text>
									<View style={styles.textInputContainer}>
										<TextInput
											style={styles.textInput}
											placeholder="카메라명"
											placeholderStyle={{
												fontFamily: 'Ubuntu_400Regular',
											}}
											value={name}
											autoCapitalize="none"
											autoCompleteType="off"
											autoCorrect={false}
											onChangeText={(text) => setName(text)}
										/>
									</View>

								<Text style={{ color: Colors.black, fontSize: 16 }}>IP</Text>
									<View style={styles.textInputContainer}>
										<TextInput
											style={styles.textInput}
											placeholder="https://127.0.0.1:1443"
											placeholderStyle={{
												fontFamily: 'Ubuntu_400Regular',
											}}
											value={ip}
											autoCompleteType="off"
											autoCorrect={false}
											onChangeText={(text) => setIP(text)}
										/>
									</View>
									<TouchableOpacity
										onPress={() => {
											storeDevice();
											setModalVisible(!modalVisible);				
										}}
										disabled={loading}
										style={{
											flexDirection: 'row',
											marginTop: 20,
										}}
									>
										<View style={styles.button}>
											{loading ? (
												<ActivityIndicator color="#fff" />
											) : (
												<Text bold style={{ fontSize: 16, color: 'white' }}>
													등록하기
												</Text>
											)}
										</View>
									</TouchableOpacity>
									<TouchableOpacity
										onPress={() => {
											setModalVisible(!modalVisible);
										}}
										disabled={loading}
										style={{
											flexDirection: 'row',
											marginTop: 20,
										}}
									>
									<View style={styles.button}>
										{loading ? (
											<ActivityIndicator color="#fff" />
										) : (
											<Text bold style={{ fontSize: 16, color: 'white' }}>
												취소
											</Text>
										)}
									</View>
								</TouchableOpacity>
								</View>
							</View>
						</Modal>

						<TouchableOpacity
							onPress={() => {
								setModalVisible(true);
							}}
							style={{
								backgroundColor: Colors.danger,
								padding: 20,
								paddingHorizontal: 120,
								marginTop: 10,
								borderRadius: 10,
							}}
						>
							<Text style={{ color: 'white', fontSize: 20}} bold>
								등록하기
							</Text>
						</TouchableOpacity>
					</View>
				</ScrollView>
		</Layout>
	);
}
const styles = StyleSheet.create({
	container: {
		flex: 1,
		flexDirection: 'column',
		backgroundColor:"gray"
	},
	textInputContainer: {
		marginTop: 15,
		backgroundColor: '#FFF',
		borderColor: '#d8d8d8',
		borderWidth: 1,
		borderRadius: 8,
		flexDirection: 'row',
	},
	textInput: {
		padding: 10,
		paddingHorizontal: 20,
		textAlign: 'left',
		color: 'black',
		flex: 1,
		fontFamily: 'Ubuntu_400Regular',
	},
	button: {
		flex: 1,
		alignItems: 'center',
		justifyContent: 'center',
		padding: 15,
		backgroundColor: Colors.primary,
		borderRadius: 8,
	},
	card: {
		backgroundColor: '#fff',
		flex: 1,
		borderTopLeftRadius: 10, // to provide rounded corners
		borderTopRightRadius: 10, // to provide rounded corners
		marginLeft: 10,
		marginRight: 10,
	},
	modalView: {
		margin: 20,
		backgroundColor: "white",
		borderRadius: 20,
		padding: 35,
		alignItems: "center",
		shadowColor: "#000",
		shadowOffset: {
		width: 0,
		height: 2
		},
		shadowOpacity: 0.25,
		shadowRadius: 3.84,
		elevation: 5
	},
	openButton: {
		backgroundColor: "#F194FF",
		borderRadius: 20,
		padding: 10,
		elevation: 2
	},
	textStyle: {
		color: "white",
		fontWeight: "bold",
		textAlign: "center"
	},
	modalText: {
		marginBottom: 15,
		textAlign: "center"
	}
	
});
